<script>
import * as d3 from 'd3'
export default {
  name: 'maps',
  data(){
    return {
      width:document.documentElement.clientWidth * 2/3,
      height:document.documentElement.clientHeight-20,
      geo_data:{
        inner:{},
        outer:{},
        outer2:{},
      },
      event_data:[],
      date_index : 0,
      cumulated:true,
      time_flag : false,
      timer : 0,
      color :d3.scale.category10(),
      color_scale : d3.scale.category20c(),
      color_scale2 : d3.scale.category20(),
      type_list:['无症状','轻型','普通型','未公开','总数'],
      show_depth :6,
      line_timer:0,
      now_depth :-1,
      chain_selected:'未选择',
      show_select:false,
      tip:false,
      mouseXY:[],
      tip_data:{},
      district:'',
      dis_flag : false,
      axis_flag:false,
      vertical:0,
      kMeans:1,

      
      
      
      
      
    }
  },
  mounted(){
    //document.documentElement.removeAttribute('theme-mode');
    //document.documentElement.setAttribute('theme-mode', 'dark');
    d3.json("北京市.json",d=>{
      this.geo_data.inner = d
    })

    d3.json("河北省.json",d=>{
      this.geo_data.outer = d
    })

    d3.json("天津市.json",d=>{
      this.geo_data.outer2 = d
    })

    d3.csv("event2.csv",d=>{
      this.event_data = d
    })

    d3.select("#mainsvg")
    .call(
			d3.behavior.zoom()
			.scaleExtent([1,30])
			.on("zoom",zoom));
		
		
	
		
		function zoom(){
			d3.selectAll(".zoom").attr("transform",
			"translate("
			+ d3.event.translate
			+ ")scale("
			+ d3.event.scale
			+ ")");
		}
    
  },
  computed:{
    line_scale(){
      let xS = d3.scale.linear().domain([0,this.date_list.length - 1]).range([30,this.axis_config.width + 30]);
			let yS = d3.scale.linear().domain([100,-10]).range([25,this.axis_config.height + 25]);
      return [xS,yS]
    },
    tip2_data(){
      if(typeof(this.tip_data) == 'string' && this.district_list.indexOf(this.tip_data) != -1){
        let tmp = this.bar_data.filter(d=>d.name == this.tip_data.substring(0,3))[0]
        let ret1 = ['总数','轻型','普通型','无症状','未公开']
        let ret2 = [tmp['count'],tmp['轻型'],tmp['普通型'],tmp['无症状'],tmp['未公开']]
        return [ret1,ret2]

      
      }
      return [[],[]]
    },
    sta_data(){
      let ret = {}
      for(let i = 0;i<this.date_list.length;i++){
        let obj = new Object()
        let leiji = 0
        let wuzhengzhuang = 0
        let quezhen = 0
        for(let j = 0;j<this.chain_id.length;j++){
          let tmp = this.daily2[this.chain_id[j]]
          if(tmp.date <= this.date_list[i]){
            leiji++
          }
          if(tmp.date == this.date_list[i]){
            if(tmp.type == '无症状'){
              wuzhengzhuang++ 
            }
            else{
              quezhen++
            }
          }
          
        }
        let dangri = wuzhengzhuang + quezhen
        obj.leiji = leiji
        obj.dangri  = dangri
        obj.wuzhengzhuang = wuzhengzhuang
        obj.quezhen = quezhen
        obj.zengfu = leiji == 0? 0 : leiji == dangri? 1:dangri / (leiji - dangri)
        
        ret[this.date_list[i]] = obj
      }



      return ret
    },
    chain_id(){
      let ret = []
      if(this.chain_selected == '未选择'){
        for(let i = 0;i<this.daily.length;i++){
          ret.push(this.daily[i].id)
        }
      }
      else{
        let obj
        for(let i = 0;i<this.chain.length;i++){
          if(this.chain[i].id == this.chain_selected){
            obj = this.chain[i]
            break
          }
        }
        let lst = [obj]
        while(lst.length > 0){
          for(let i = 0;i<lst.length;i++){
            let tmp_node = lst.shift()
            ret.push(tmp_node.id)
            for(let j = 0;j<tmp_node.children.length;j++){
              lst.push(tmp_node.children[j])
            }
          }
        

        }
      }
      return ret
    },
    chain_data(){
      let ret = []
      for(let i = 0;i<this.chain.length;i++){
        ret.push(this.bfs(this.chain[i]))
      }
      return ret
    },
    depth_list(){
      let ret = []
      for(let i = 0;i<this.show_depth;i++){
        ret.push(i)
      }
      return ret
    },
    
    level(){
      let ret =[]
      var traverse = (node,depth) => {
        for(let i = 0;i<node.children.length;i++){
            ret.push([node.id,node.children[i].id,depth])
            if(node.children[i].children.length >0){
              traverse(node.children[i],depth + 1)
            }
        }
      }
      for(let i = 0;i<this.chain.length;i++){
        traverse(this.chain[i],0)
      }
      return ret
    },
    
    chain(){
      let ret = []
      let tree_dic = {}
      for(let i = 0;i<this.daily.length;i++){
        let id = this.daily[i].id
        if(id != 'Unknown'){
          tree_dic[id] = {}
        }
      }
      //console.log(tree_dic)

      for(let i = 0;i<this.daily.length;i++){
        let id = this.daily[i].id
        let parent = this.daily[i].source
        if(id != 'Unknown'){

            let tmp = new Object()
            tmp.parent = parent == 'Unknown'? 'Unknown':tree_dic[parent]
            tmp.children = []
            tmp.id = id
            tree_dic[id] = tmp
          
        }
      }

      for(let i = 0;i<this.daily.length;i++){
        let id = this.daily[i].source
        let child = this.daily[i].id
        if(id != 'Unknown'){
          //console.log(tree_dic[id],id)
          tree_dic[id].children.push(tree_dic[child])
        }
      }

      for(let key in tree_dic){
        if(tree_dic[key].parent == 'Unknown' && tree_dic[key].children.length > 0){
          ret.push(tree_dic[key])
        }
      }
      return ret
    },
    line_data(){
      let ret = []
      let type_lst = ['无症状','轻型','普通型','未公开']
      for(let i = 0;i<type_lst.length;i++){
        let tmp_lst = []
        for(let j = 0;j<this.date_list.length;j++){
          let tmp = new Object()
          tmp.x = j
          let count = this.daily.filter(d=>d.date == this.date_list[j] && d.type == type_lst[i]).length

          tmp.y = count
          tmp_lst.push(tmp)
        }
        ret.push(tmp_lst)
      }
      let tmp_lst = []
        for(let j = 0;j<this.date_list.length;j++){
          let tmp = new Object()
          tmp.x = j
          let count = this.daily.filter(d=>d.date == this.date_list[j]).length

          tmp.y = count
          tmp_lst.push(tmp)
        }
        ret.push(tmp_lst)
      //return ret
      return d3.layout.stack()
				.offset(0)(ret)
     // return ret
    },
    line_g(){
      var xS = d3.scale.linear().domain([0,this.date_list.length - 1]).range([30,this.axis_config.width + 30]);
			var yS = d3.scale.linear().domain([100,-10]).range([25,this.axis_config.height + 25]);
      return d3.svg.line()
			.interpolate("basis")
			.x(function(d){return xS(d.x);})
			.y(function(d){return yS(d.y);});
      //+d.y0
    },
    axis_config(){
      return {
        width:this.width/2 - 25 -60,
        height:this.height/3 - 5 - 50 ,
      }
    },
    watcher(){
      return this.bar_data.length
    },
    rank(){
      let ret = []
      for(let i = 0;i<this.bar_data.length;i++){
        ret.push(this.bar_data[i].name)
      }
      return ret
    },
    length_scale(){
      return d3.scale.linear().domain([0,750]).range([100,this.width /2 ])
    },
    height_scale1(){
      
      return d3.scale.linear().domain([0,Math.max(...this.tip2_data[1]) + 1]).range([20,100])
    },
   
    bar_data(){ 
      let ret = []
      for(let i = 0;i<this.district_list.length;i++){
        let dist = this.district_list[i]
        let dic = new Object()
        dic.name = dist
        let addup = this.show_data.filter(d=>d.district== dist).length
        dic.count = addup
        dic['轻型'] = this.show_data.filter(d=>d.district== dist && d.type == '轻型').length
        dic['普通型'] = this.show_data.filter(d=>d.district== dist&& d.type == '普通型').length
        dic['无症状'] = this.show_data.filter(d=>d.district== dist&& d.type == '无症状').length
        dic['未公开'] = this.show_data.filter(d=>d.district== dist&& d.type == '未公开').length
        ret.push(dic)
      }

      return ret.sort((a,b)=>b.count - a.count)
      
    },
    district_list(){
      let se = new Set()
      for(let i = 0;i<this.daily.length;i++){
        se.add(this.daily[i].district)
      }
      return Array.from(se)
    },
    show_data(){
      if(this.cumulated){
        return this.daily.filter(d=>d.date<=this.date_select && this.chain_id.indexOf(d.id) != -1)
      }
      else{
        return this.daily.filter(d=>d.date==this.date_select && this.chain_id.indexOf(d.id) != -1)
      }
      

    },  
    date_select(){
      return this.date_list[this.date_index]
    },
    date_list(){
      let se = new Set()
      for(let i = 0;i<this.daily.length;i++){
        se.add(this.daily[i].date)
      }
      return Array.from(se).sort((a,b)=>a-b)
    },
    daily(){
      let lst = []
      for(let i = 0;i<this.event_data.length;i++){
        let tmp_data = this.event_data[i]
        let date = parseInt(tmp_data['24时日期'].substring(0,4) +
                   tmp_data['24时日期'].substring(5,7) + 
                   tmp_data['24时日期'].substring(8,10))
        if(date){
          let new_data = new Object()
          new_data.date = date
          new_data.id = tmp_data['表格ID']
          new_data.index = tmp_data['表格ID']
          new_data.daily_index = tmp_data['每日编号']
          new_data.type = tmp_data['症状']?tmp_data['症状']:'未公开'
          new_data.district = tmp_data['区']
          new_data.address = tmp_data['居住地址']
          new_data.source = tmp_data['上游表格ID']==0?  'Unknown':tmp_data['上游表格ID']
          new_data.source_address = tmp_data['上游地址']
          new_data['2'] = tmp_data['kmean_2']
          new_data['3'] = tmp_data['kmean_3']
          new_data['4'] = tmp_data['kmean_4']
          new_data['5'] = tmp_data['kmean_5']
          new_data['6'] = tmp_data['kmean_6']
          new_data['7'] = tmp_data['kmean_7']
          new_data['8'] = tmp_data['kmean_8']
          new_data['9'] = tmp_data['kmean_9']
          new_data['10'] = tmp_data['kmean_10']
          new_data.data_problem = tmp_data['数据问题']?tmp_data['数据问题']:'No problem.'
          let space = tmp_data['坐标'].substring(6,tmp_data['坐标'].length-1).split(' ')
          new_data.xy = space.map(x=>parseFloat(x))
          let source_space = tmp_data['上游坐标']? tmp_data['上游坐标'].substring(6,tmp_data['坐标'].length-1).split(' '):0
          new_data.source_xy = source_space? source_space.map(x=>parseFloat(x)):0
          lst.push(new_data)

        }
        
      }

      return lst
    },
    daily2(){
      let dic = {}
      for(let i = 0;i<this.daily.length;i++){
        if(this.daily[i].id != 'Unknown'){
          dic[this.daily[i].id] = this.daily[i]
        }
      }

     
        
      
      return dic
    },
    projection(){
      return d3.geo.mercator().center([116.418757,40.47544]).scale(34000 * this.width/1800)
    },
    map_path(){
      return d3.geo.path().projection(this.projection)
    },
    style(){
      return "background-color:"+"black" +";position:absolute;border-radius: 15px;left:10px;top:10px;border:1.5px solid #0E021B;width:"+this.width+"px;"+"height:"+this.height+ "px;" 
    },
    style2(){
      return "background-color:"+"black" +";position:absolute;border-radius: 15px;top:10px;left:"+(this.width + 15)+";border:1.5px solid #0E021B;width:"+(this.width/2 - 25)+"px;"+"height:"+this.height * 2/3+ "px;" 
    },

    style3(){
      return "background-color:"+"black" +";position:absolute;border-radius: 15px;top:"+(this.height *2/3 + 15) + "px;left:"+(this.width + 15)+";border:1.5px solid #0E021B;width:"+(this.width/2 - 25)+"px;"+"height:"+(this.height/3 - 5)+ "px;" 
    },
    show_id(){
      let lst = []
      for(let i = 0;i<this.show_data.length;i++){
        lst.push(this.show_data[i].id)
      }
      return lst
    }
  },
  methods:{
    update_vertical(){
      let vertical = 0
      if(this.axis_flag){
        let mouseX = event.offsetX
        vertical = Math.floor((mouseX - 30)/((this.axis_config.width / (this.date_list.length + 1 ))))
        
      }
      this.vertical = Math.min(Math.max(0,vertical),this.date_list.length-1)
    },
    mouse_off(){
      this.tip = false
      this.tip_data = {}
      this.mouseXY = []
    },
    update_mouse(d,type){
      if(!this.tip){
        this.tip_data = d
      }
      
      this.mouseXY = [event.offsetX,event.offsetY]
      this.tip = type
      
    },
    update_board(){
      
      function tick(){
			d3.select("#dots").selectAll("circle")
			.attr("cx",function(d){return d.x;})
			.attr("cy",function(d){return d.y;})

      d3.select("#dots").selectAll("text")
			.attr("dx",function(d){return d.x;})
			.attr("dy",function(d){return d.y + 3;})
      }

      var force = d3.layout.force()
                  .size([300,300])
                  .gravity(0.2)
                  .charge(-300)
                  .friction(0.7)
                  .on('tick',tick)
                  .nodes(this.chain_data);
      

      d3.select("#dots").selectAll("circle")
      .data(this.chain_data)
      .enter()
      .append("circle")
      .attr("cx",100)
      .attr("cy",100)
      .attr("r",d=>d.num/20+12)
      .attr("fill",'black')
      .attr("stroke",(d,i)=>this.color_scale(i))
      .on("mouseover",function(){
        d3.select(this)
        .transition()
        .attr("stroke-width",3)
      }
      )
      .on("mouseout",function(){
        d3.select(this)
        .transition()
        .attr("stroke-width",1)
      })
      .on('click',d=>{
        if(this.chain_selected == d.id){
          this.chain_selected = '未选择'
        }
        else{
          this.chain_selected = d.id
        }
        
      })
      .call(force.drag)

      d3.select("#dots").selectAll("text")
      .data(this.chain_data)
      .enter()
      .append('text')
      .attr("text-anchor","middle")
      .attr("dx",100)
      .attr("dy",100)
      .attr("font-size",d=>d.num/100 + 10)
      .attr("fill",'white')
      .on('click',d=>{
        if(this.chain_selected == d.id){
          this.chain_selected = '未选择'
        }
        else{
          this.chain_selected = d.id
        }
        
      })
      .text(d=>d.id)
      

      

      force.start()
    },
    bfs(obj){
      let lst = [obj]
      let max_depth = 0
      let inf = 0
      let id = obj.id
      while(lst.length > 0){
        for(let i = 0;i<lst.length;i++){
          let tmp_node = lst.shift()
          inf++
          for(let j = 0;j<tmp_node.children.length;j++){
            lst.push(tmp_node.children[j])
          }
        }
        max_depth++ 

      }
      return {id:id,depth:max_depth,num:inf}
        
    },
    judge_in_show_data(id){
      
      
      if(this.show_id.indexOf(id) != -1){
        return true
      }
      else{
        return false
      }
    },
    update_line(){

      d3.select("#chain").selectAll("line")
      .data(this.level)
      .enter()
      .append("line")
      .attr("x1",d=>this.projection(this.daily2[d[0]].xy)[0])
      .attr("y1",d=>this.projection(this.daily2[d[0]].xy)[1])
      .attr("x2",d=>this.projection(this.daily2[d[0]].xy)[0])
      .attr("y2",d=>this.projection(this.daily2[d[0]].xy)[1])
      .attr("stroke",'yellow')
      

      d3.select("#chain").selectAll("line")
      .data(this.level)
      .filter(d=>this.judge_in_show_data(d[0]) && this.judge_in_show_data(d[1]))
      .transition()
      .duration(1500)
      .attr("x2",d=>this.projection(this.daily2[d[1]].xy)[0])
      .attr("y2",d=>this.projection(this.daily2[d[1]].xy)[1])
      
      d3.select("#chain").selectAll("line")
      .data(this.level)
      .filter(d=>!this.judge_in_show_data(d[0]) || !this.judge_in_show_data(d[1]))
      .transition()
      .duration(1500)
      .attr("x2",d=>this.projection(this.daily2[d[0]].xy)[0])
      .attr("y2",d=>this.projection(this.daily2[d[0]].xy)[1])


      /*
        <g v-for = '(depth,index) in depth_list' 
    :key = 'index'
    :id = '"infection_link" + depth'>
      <g v-for = '(d,i) in level'
      :key = 'i'>
        <line class = 'infection_link'
        v-if = 'd[2] <= now_depth &&d[2] == depth && daily2[d[0]].date <= date_select && daily2[d[1]].date <= date_select'
        :x1 = 'projection(daily2[d[0]].xy)[0]'
        :x2 = 'projection(daily2[d[1]].xy)[0]'
        :y1 = 'projection(daily2[d[0]].xy)[1]'
        :y2 = 'projection(daily2[d[1]].xy)[1]'
        stroke = 'gold'
        stroke-width = '2'
        stroke-opacity = 0.7>

        </line>

      </g>
    </g>
      */

    },
    
    log(){
      console.log(this.event_data)
      console.log(this.daily)
      console.log(this.date_list)
      console.log(this.show_data)
      console.log(this.district_list)
      console.log(this.bar_data)
      console.log(this.line_data)
      console.log(this.chain)
      console.log(this.level)
      console.log(this.daily2)
      console.log(this.chain_data)
      console.log(this.chain_id)
      console.log(this.sta_data)
      console.log(this.tip_data)
      console.log(this.district)
      console.log(this.tip2_data)
      //this.link_dash(0)
      
      
     
    },
    rank_updater(){
      d3.select("#ranking").selectAll(".bars")
      .data(this.district_list)
      .enter()
      .append("rect")
      .attr("class",'bars')
      .attr("x",20)
      .attr("height", this.height * 2 / 3 / this.district_list.length - 10)
      .attr("stroke-opacity",0)
      d3.select("#ranking").selectAll(".bars")
      .data(this.district_list)
      .transition()
      .duration(800)
      .attr("y",d=>this.rank.indexOf(d) * (this.height * 2 / 3 / this.district_list.length - 5) + 50)
      .attr("stroke",d=>this.color_scale(d))
      .attr("width",d=>this.length_scale(this.bar_data[this.rank.indexOf(d)].count))
      .attr("stroke-opacity",1)
      d3.select("#ranking").selectAll(".texts")
      .data(this.district_list)
      .enter()
      .append("text")
      .attr("class",'texts')
      .attr("fill",'white')
      .attr("font-size",14)
      .attr("text-anchor",'middle')
      .attr("fill-opacity",0)
      d3.select("#ranking").selectAll(".texts")
      .data(this.district_list)
      .transition()
      .duration(800)
      .attr("dx",d=>this.length_scale(this.bar_data[this.rank.indexOf(d)].count) /2 + 20)
      .attr("dy",d=>this.rank.indexOf(d) * (this.height * 2 / 3 / this.district_list.length - 5) + 65)
      .attr("fill-opacity",1)
      .text(d=>d + ' ：' + this.bar_data[this.rank.indexOf(d)].count)
    },
    

  },
  watch:{
    time_flag(val){
      if(val){
        this.timer = setInterval(()=>{
          if(this.date_index < this.date_list.length - 1){
            this.date_index ++ 
          }
          else{
            this.time_flag = false
          }
        },2000)
      }
      else{
        clearInterval(this.timer)
      }
    },
    date_index(){
      this.rank_updater()
      if(this.cumulated){
        //this.update_line()
      }
      else{
        d3.select("#chain").selectAll("line").remove()
      }
      
    },
    chain_selected(){
      //this.update_line()
      d3.select("#dots").selectAll("circle")
      .filter(d=>d.id == this.chain_selected)
      .transition()
      .attr("fill",'white')
      .attr("fill-opacity",0.5)
      d3.select("#dots").selectAll("circle")
      .filter(d=>d.id != this.chain_selected)
      .transition()
      .attr("fill",'black')
      .attr("fill-opacity",1)
    },
    watcher(){
      this.rank_updater()
      this.update_board()
    }
  
  }
}
</script>

